<template>
    <div class="content-manage-box">
        <div class="ibox">
            <div class="ibox-title">
                <h3>
                    <div class="pull-right">
                        <el-tooltip class="item" effect="dark" content="导出当前筛选条件下最多 5000 条数据" placement="top">
                            <el-button @click="handleExportData($event)" size="small" icon="upload">导出数据</el-button>
                        </el-tooltip>
                    </div>
                    列表
                    <el-tooltip :content="'当前内容为' + (filter.content_type == 1 ? '文章' : '视频') "
                                placement="top">
                        <el-switch
                                v-model="filter.content_type"
                                @change="filterAction()"
                                on-color="#13ce66"
                                off-color="#ff4949"
                                on-text="文章"
                                off-text="视频"
                                on-value="1"
                                off-value="3">
                        </el-switch>
                    </el-tooltip>
                </h3>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="标题">
                        <el-input placeholder="标题"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.title">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="ID">
                        <el-input placeholder="ID"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="来源名称">
                        <el-input placeholder="来源名称"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.source_name">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="后台用户ID">
                        <el-input placeholder="后台用户ID"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.admin_member_id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="属性">
                        <el-checkbox v-model="filter.is_hot" @change="filterAction()" true-label="2" false-label="0">
                            热推
                        </el-checkbox>
                        <el-checkbox v-model="filter.is_top" @change="filterAction()" true-label="2" false-label="0">
                            置顶
                        </el-checkbox>
                    </el-form-item>
                </el-form>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <!--<el-form-item label="文章类型">-->
                        <!--<el-select-->
                            <!--size="small"-->
                            <!--@change="filterAction()"-->
                            <!--v-model="filter.source_name_type">-->
                            <!--<el-option label="全部" value="0"></el-option>-->
                            <!--<el-option-->
                                <!--v-for="(item, $key) in sourceNameTypes"-->
                                <!--:label="item"-->
                                <!--:value="$key"-->
                                <!--:key="$key">-->
                            <!--</el-option>-->
                        <!--</el-select>-->
                    <!--</el-form-item>-->

                    <el-form-item label="分类">
                        <el-select
                                size="small"
                                @change="filterAction()"
                                v-model="filter.type">
                            <el-option label="全部" value=""></el-option>
                            <el-option
                                    v-for="item in articleTypes"
                                    :label="item.name"
                                    :value="item.id.toString()"
                                    :key="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="二级标签">
                        <article-tags
                                v-model="filter.tag"
                                @change="filterAction()"
                                type="article"
                                :category="filter.type">
                        </article-tags>
                    </el-form-item>

                    <el-form-item label="状态">
                        <el-select
                                size="small"
                                v-model="filter.status"
                                @change="filterAction()">
                            <el-option label="全部" value=""></el-option>
                            <el-option
                                    v-for="(item, $key) in articleStatus"
                                    :label="item"
                                    :value="$key"
                                    :key="item"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="tips">
                        <el-select
                                size="small"
                                v-model="filter.tips"
                                @change="filterAction()">
                            <el-option label="全部" value=""></el-option>
                            <el-option
                                    v-for="item in articleTips"
                                    :label="item"
                                    :value="item"
                                    :key="item">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="延迟">
                        <el-select
                                size="small"
                                v-model="filter.is_delay"
                                @change="filterAction()">
                            <el-option label="全部" value=""></el-option>
                            <el-option
                                    v-for="(item, $key) in articleDelay"
                                    :label="item"
                                    :value="$key"
                                    :key="item">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="操作">
                        <el-button @click="filterAction($event)" type="primary" icon="search">搜索</el-button>
                        &nbsp;&nbsp;&nbsp;
                        <router-link :to="{ name: 'PushContentEdit', query: { type: filter.type } }">
                            <el-button type="info" icon="edit">新建文章 </el-button>
                        </router-link>
                    </el-form-item>

                </el-form>
            </div>
            <div class="ibox-filter">
                <el-popover
                        ref="modifyCatePopover"
                        placement="top"
                        width="200"
                        v-model="modifyCate.visible">
                    <div>
                        修改分类：
                        <el-select
                                v-model="modifyCate.type">
                            <el-option label="不修改" value=""></el-option>
                            <el-option
                                    v-for="item in articleTypes"
                                    :label="item.name"
                                    :value="item.id"
                                    :key="item.id"></el-option>
                        </el-select>
                    </div>
                    <div style="text-align: right; margin: 0">
                        <el-button size="mini" type="text" @click="modifyCate.visible = false">取消</el-button>
                        <el-button type="primary" size="mini" @click="handleBatchAction($event, 'batchEditCategory', { modifyCate: modifyCate, articleTypesMap: articleTypesMap })">确定</el-button>
                    </div>
                </el-popover>

                <span class="el-icon-setting"></span> 批量处理：

                <el-button size="small" v-popover:modifyCatePopover>修改分类</el-button>
                <el-button type="defalut" size="small" @click.native="handleBatchAction($event, 'batchModify')">删除文章</el-button>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        热推<i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchHot', { action: 'on' })">
                            <span>热推到本分类</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchHot', { action: 'on', isRecommend: true })">
                            <span>热推到推荐</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchHot', { action: 'off' })">
                            <span>取消热推</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        标签<i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchTips', { action: 'on', tip: '实时' })">
                            <span>实时标签</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchTips', { action: 'on', tip: '热门' })">
                            <span >热门标签</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchTips', { action: 'off', tip: '' })">
                            <span >取消标签</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        评论<i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchComment', { action: 'on'})">
                            <span>开启评论</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchComment', { action: 'off'})">
                            <span>关闭评论</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        分享<i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchShare', { action: 'on'})">
                            <span >开启分享</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchShare', { action: 'off'})">
                            <span>关闭分享</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-dropdown>
                    <el-button icon="setting" size="small" type="">
                        广告<i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchSkipAd', { action: 'on'})">
                            <span>开启广告</span>
                        </el-dropdown-item>
                        <el-dropdown-item @click.native="handleBatchAction($event, 'batchSkipAd', { action: 'off'})">
                            <span>关闭广告</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

            </div>
            <div class="ibox-content">
                <div class="control">
                    <el-table :data="filterListDate"
                              @selection-change="handleSelectCheckbox"
                              style="width: 100%"
                              element-loading-text="拼命加载中"
                              v-loading="loading">
                        <el-table-column type="expand">
                            <template scope="props">
                                <el-form label-position="left"
                                         inline
                                         class="props-table-expand">
                                    <el-form-item label="分享">
                                        <span>{{ props.row.share_count }}</span>
                                    </el-form-item>
                                    <el-form-item label="创建时间">
                                        <span>{{ props.row.create_time }}</span>
                                    </el-form-item>
                                    <el-form-item label="采集者">
                                        <span>{{ props.row.picker_name + '\n' + props.row.picker_id }}</span>
                                    </el-form-item>
                                </el-form>
                            </template>
                        </el-table-column>
                        <el-table-column type="selection"
                                         width="35px">
                        </el-table-column>

                        <el-table-column label="文章 ID"
                                         prop="id"
                                         width="80">
                        </el-table-column>

                        <el-table-column label="封面"
                                         width="80px" class-name="cell-cover cell-badge">
                            <template scope="scope">
                                <div class="covers" @click="showPreviewCover($event, scope.row)">
                                    <img v-for="cover in scope.row.cover"
                                        :src="(cover) + '?imageView2/1/w/30/h/30'"
                                        width="30" height="30" alt="">
                                </div>
                                <div class="content-badge">
                                    <el-tag v-text="['', '左图', '右图', '三图', '大图', '长图', '无图'][scope.row.cover_show_type]"></el-tag>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column label="标题"
                                         min-width="180"
                                         class-name="cell-badge">
                            <template scope="scope">
                                <a
                                        v-text="scope.row.title"
                                        :title="scope.row.title"
                                        :href="getContentLink(scope.row)"
                                        target="_blank"
                                        class="font-fa"></a>
                                <div class="content-badge">
                                    <el-tag v-show="scope.row.is_top == 2" type="primary">top</el-tag>
                                    <el-tag v-show="scope.row.is_hot == 2" :type="scope.row.is_hot_promote ? 'danger': 'primary'">hot</el-tag>
                                    <el-tag v-show="scope.row.tips" color="#1C75BF">{{ scope.row.tips }}</el-tag>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column label="状态"
                                         prop="statusName"
                                         width="60px">
                        </el-table-column>
                        <el-table-column label="类型"
                                         prop="typeName"
                                         width="80px">
                        </el-table-column>
                        <el-table-column label="来源"
                                         prop="source_name"
                                         width="140px">
                        </el-table-column>
                        <el-table-column label="浏览"
                                         prop="read_count"
                                         width="80px">
                        </el-table-column>
                        <el-table-column label="评论"
                                         width="80px">
                            <template scope="scope">
                                <router-link :to="{ name: 'CommentManage', query: { 'content_id': scope.row.id, 'status': COMMENT.PASS_CANDIDATE }}">
                                    {{scope.row.comment_count}}
                                </router-link>
                            </template>
                        </el-table-column>
                        <el-table-column label="发布者"
                                         width="100px">
                            <template scope="scope">
                                <span>{{ scope.row.admin_member_nickname + '(ID:' + scope.row.admin_member_id + ')' }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="发布时间"
                                         width="150px"
                                         prop="publish_time">
                        </el-table-column>
                        <el-table-column label="操作" width="280px">
                            <template scope="scope">
                                <router-link v-show="filter.content_type == 1" :to="'content-edit?id=' + scope.row.id">
                                    <el-button
                                            size="small"
                                            icon="edit"
                                            type="default">
                                    </el-button>
                                </router-link>

                                <el-button
                                        v-if="scope.row.status == 2"
                                        v-text=" scope.row.is_hot == 2 ? '取消热推' : '热推' "
                                        size="small"
                                        type="info"
                                        @click="handleSetHot($event, scope.$index, scope.row)">热推
                                </el-button>

                                <el-button
                                        v-if="scope.row.status != 3"
                                        size="small"
                                        type="danger"
                                        @click="handleDelete($event, scope.$index, scope.row)">删除
                                </el-button>

                                <el-dropdown>
                                    <el-button size="small" type="">
                                        更多<i class="el-icon-caret-bottom el-icon--right"></i>
                                    </el-button>
                                    <el-dropdown-menu slot="dropdown">

                                        <el-dropdown-item v-if="scope.row.status == 2">
                                            <span @click="handleRefershCDN($event, scope.$index, scope.row)">刷新cdn</span>
                                        </el-dropdown-item>

                                        <el-dropdown-item v-if="scope.row.status == 2 && scope.row.is_hot != 2">
                                            <span @click="handleSetHot($event, scope.$index, scope.row, true)">热推到推荐</span>
                                        </el-dropdown-item>
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>

                <div class="control">
                    <el-pagination @current-change="handleCurrentChange"
                                   :current-page="filter.page"
                                   :page-count="totalPage"
                                   layout="total, prev, pager, next, jumper">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import ArticleTags from '../../widget/ArticleTags.vue';
    import moment from '../../module/MyMoment';
    import q from 'q';
//    import storeFilterMixin from 'src/mixin/storeFilterMixin';
    import PreviewCover from '../../components/preview-cover';
    import QuRequest from '../../core/QuRequest';
    import ArticleStatus from '../../module/ArticleStatus';
    import ContentModel from '../../module/Content';
    import Auth from '../../core/Auth';
//    import deepcopy from 'deepcopy';

    const quRequest = new QuRequest();

    import {
        COMMENT
    } from '../../module/CommentConfig';
//
//    import fixedTableMixin from 'src/mixin/fixedTableMixin';
//
    let contentModel = null;
//    let quRequest = null;
    let previewCover = null;

    const MODIFY_ACTIONS = {
        'batchModify': options => {
            return {
                api: quRequest.Apis.qukan.content.modifyContentStatus,
                getData(item) {
                    return {
                        id: item.id,
                        status: 3
                    }
                },
                handleSuccess(item) {
                    item.status = '3';
                },
                getConfirmMessage() {
                    return `请确认批量 "删除" 操作`;
                }
            }
        },
        'batchComment': options => {
            // form.can_comment, 1 | 2
            let { action } = options;
            let isOn = action === 'on';
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    return {
                        id: item.id,
                        can_comment: isOn ? 1 : 2
                    }
                },
                handleSuccess(item) {
                    item.can_comment = isOn ? 1 : 2
                },
                getConfirmMessage() {
                    return `请确认批量${ isOn ? '开启' : '关闭' } "评论" 操作`;
                }
            }
        },
        'batchShare': options => {
            // form.share_type: 0 | 3
            let { action } = options;
            let isOn = action === 'on'; // on | off
            let status = isOn ? '0' : '3';
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    return {
                        id: item.id,
                        share_type: status
                    }
                },
                handleSuccess(item) {
                    item.share_type = status;
                },
                getConfirmMessage() {
                    return `请确认批量${ isOn ? '开启' : '关闭' } "分享" 操作`;
                }
            }
        },
        'batchSkipAd': options => {
            // form.extend.skip_ad: 0 | 1
            let { action } = options;
            let isOn = action === 'on'; // on | off
            let status = isOn ? '0' : '1';
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    let { extend = {} } = item;
                    extend = typeof extend === 'object' ? extend : {};
                    extend.skip_ad = status;
                    return {
                        id: item.id,
                        extend: JSON.stringify(extend)
                    }
                },
                handleSuccess(item) {
                    item.skip_ad = status;
                },
                getConfirmMessage() {
                    return `请确认批量${ isOn ? '开启' : '关闭' } "广告" 操作`;
                }
            }
        },
        'batchTips': options => {
            let { action } = options;
            let isOn = action === 'on';
            return {
                api: quRequest.Apis.qukan.content.setTips,
                getData(item) {
                    return {
                        id: item.id,
                        tips: options.tip
                    }
                },
                handleSuccess(item) {
                    item.tips = options.tip;
                },
                getConfirmMessage() {
                    return `请确认批量${ isOn ? '开启' : '取消' } "${ options.tip || '' }标签" 操作`;
                }
            }
        },
        'batchHot': options => {
            let { action, isRecommend } = options;
            let isOn = action === 'on';
            let status = isOn ? 2 : 1;
            let type = isRecommend ? 255 : -1;
            return {
                api: quRequest.Apis.qukan.content.setHot,
                getData(item) {
                    return {
                        content_id: item.id,
                        status: status,
                        type: type
                    }
                },
                handleSuccess(item) {
                    item.is_hot = status;
                },
                getConfirmMessage() {
                    if (!isOn) {
                        return `请确认批量 "取消热推" 操作`;
                    } else {
                        return `请确认批量 "热推到${ isRecommend ? '推荐分类' : '本分类' }" 操作`
                    }
                }
            }
        },
        'batchEditCategory': options => {
            let { modifyCate, articleTypesMap } = options;
            return {
                api: quRequest.Apis.qukan.content.modifyContent,
                getData(item) {
                    return {
                        id: item.id,
                        type: modifyCate.type,
                    }
                },
                handleSuccess(item) {
                    item.type = modifyCate.type;
                },
                getConfirmMessage() {
                    return `请确认批量修改分类 "${ articleTypesMap[modifyCate.type] }" 操作`;
                }
            }
        }
    };

    class BatchAction {
        constructor({ api, getData, handleSuccess, getConfirmMessage }) {
            return {
                create(item) {
                    return quRequest.send(api, getData(item)).then(res => {
                        if (res.code == 0) {
                            handleSuccess&& handleSuccess(item)
//                            _.isFunction(handleSuccess) ? handleSuccess(item) : '';
                        } else {
                            throw new Error(res.message || '未知错误')
                        }
                    });
                },
                confirmMessage: getConfirmMessage
            };
        }
    }

    export default {
//        mixins: [fixedTableMixin, storeFilterMixin],
        components: {
            ArticleTags
        },
        created() {
//            this.$restoreFilterStore();
            contentModel = new ContentModel()
        },
        mounted() {
            previewCover = new PreviewCover({
                autoClose: true,
                container: 'body'
            });
//            quRequest = new QuRequest();
            this.getList();
            this.getCategorys();
        },
        beforeRouteLeave(to, from, next) {
            previewCover.destory();
            next(true);
        },
        data() {
            return {
                expandAll: true,
                loading: false,
                COMMENT: COMMENT,
                filterStoreKey: 'content-filter-store',
                stat: {},
                list: [],
                articleTypes: [],
                articleTypesMap: {},
              articleStatus: ArticleStatus.maps,
                articleDelay: {
                    "1": "延迟",
                    "2": "非延迟",
                },
                articleTips: ["热门", "实时"],
                control: {
                    currentItem: {}
                },
                filter: {
                    show_type: '2,3',
                    content_type: '1',
                    id: '',
                    status: "",
                    title: "",
                    type: "",
                    tag: "",
                    tips: "",
                    is_delay: "",
                    // 1 id排序 2 发布时间 默认 1
                    order_type: "1",
                    // 1 降序 2 升序 默认 1
                    order: "1",
                    is_hot: "0",
                    page: 1,
                    source_name: '',
                    admin_member_id: '',
                    is_top: '0', // 是不是置顶. 0全部, 2是,
                    source_name_type: '0' //来源类型分类 0:全部 1:编辑发布 2:合作媒体 3:自媒体
                },
                listData: [],
                totalPage: 1,
                listSelect: [],
                modifyCate: {
                    type: "",
                    visible: false
                }
            }
        },
        computed: {
            filterListDate() {
                return this.listData.map((item) => {
                    item.publish_time = moment(item.publish_time).toFormat();
                    item.create_time = moment(item.create_time).toFormat();
                    item.typeName = this.articleTypesMap[item.type];
                    item.statusName = this.articleStatus[item.status];
                    return item;
                });
            },
            sourceNameTypes() {
                return contentModel.sourceNameTypes;
            }
        },
        methods: {
            filterAction: function ($event) {
                this.filter.page = 1;
                this.getList();
            },
            creatForm(){
                let {filter} = this;
                let arry = [];
                for( let key in filter) {
                    let value = filter[key];
                    arry.push(`<input name="${key}" value="${ value }">`);
                }
                return arry.join('');
            },
            handleExportData($event) {
                let filter = Object.assign({},this.filter);
                filter.export = 1;
                filter.token = Auth.getToken();
                let url = quRequest.getUrl(quRequest.Apis.qukan.content.getAdminContentList);
                let formHtml = `<form method="post" target="_blank" action="${ url }">
                    ${
                        this.creatForm()
                    }
                </form>`;
                let $container = document.createElement('div');
                $container.innerHTML = formHtml;
                document.body.appendChild($container);
                $container.querySelector('form').submit();
                $container.remove();
            },
            showPreviewCover: function ($event, item) {
                previewCover.show($event.target, item.cover, {
                    top: -60,
                    left: 40
                });
            },
            handleCurrentChange(page) {
                this.filter.page = page;
                this.getList();
            },
            handleSelectCheckbox (select) {
                this.listSelect = select;
            },
            handleDelete($event, index, item) {
                var $elem = $event.target;
                let {$blockRequest, $message} = this;
                $blockRequest({
                    $elem: $elem,
                    req: quRequest.send(quRequest.Apis.qukan.content.modifyContentStatus, {
                        id: item.id,
                        status: "3",
                    }),
                    pendingText: '删除中'
                }).then(res => {
                    $message.success(`删除 ${item.title.substring(0, 10)}... 成功`);
                    item.status = "3";
                }).catch(reason => {
                    $message.error(`删除失败：${reason.message || '未知原因'} `);
                    throw reason;
                }).done();
            },
            handleRefershCDN($event, $index, item) {
                let {$blockRequest, $message} = this;
                let $elem = $event.target;

                $blockRequest({
                    $elem: $elem,
                    req: quRequest.send(quRequest.Apis.qukan.content.refreshCND, {
                        id: item.id
                    })
                }).then(res => {
                    $message.success('刷新成功');
                }).catch(reason => {
                    $message.error('操作失败');
                    throw reason;
                }).done();
            },
            handleSetHot ($event, $index, item, isRecommend = false) {
                let {$blockRequest, $message} = this;
                let $elem = $event.target;
                let isHot = item.is_hot;
                let changeStatus = isHot == 2 ? 1 : 2;

                return this.$confirm(`${ changeStatus == 2 ? '请确认热推操作' : '请确认取消热推操作' }, 是否继续?`, '请确认', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    return $blockRequest({
                        $elem: $elem,
                        req: quRequest.send(quRequest.Apis.qukan.content.setHot, {
                            content_id: item.id,
                            status: changeStatus,
                            type: isRecommend ? 255 : -1,
                        })
                    });
                })
                .then(res => {
                    $message.success('操作成功');
                    item.is_hot = changeStatus;
                }).catch(reason => {
                    $message.error('操作失败');
                    throw reason;
                })
            },
            handleBatchAction($event, action, options = {}) {
                let { $message, listSelect } = this;
                let $elem = $event.target;

                return q.fcall(() => {
                    if (!listSelect.length || $elem._req_block) return false;
                    // ACTION_HANDLES[action](options)
                    let hadle = new BatchAction(MODIFY_ACTIONS[action](options));

                    return this.$confirm(`${ hadle.confirmMessage() }, 是否继续?`, '请确认', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        let promises = listSelect.map(item => {
                            return hadle.create(item);
                        });
                        $message.info('批量操作执行中...');
                        return q.allSettled(promises).then(values => {
                            let [ handelSuccess, handleError ] = [[], []];
                            let messages = [];

                            values.forEach((v) => {
                                if (v.state === "fulfilled") {
                                    handelSuccess.push(v);
                                } else {
                                    messages.push(v.reason.message);
                                    handleError.push(v);
                                }
                            });

                            if (messages.length) {
                                const h = this.$createElement;
                                this.$notify({
                                    title: '错误',
                                    message: h('div', { domProps: { innerHTML: messages.join('<br/>') } }),
                                    duration: 10 * 1000
                                })
                            }
                            return { handleError, handelSuccess };
                        });
                    }, () => {
                        $message.info('已取消批量操作');
                    });
                }).then(({ handleError, handelSuccess } = {}) => {
                    if (handleError || handelSuccess) {
                        $message.info(`批量处理成功：${ handelSuccess.length }条，失败：${ handleError.length }条`);
                    }
                }).catch(error => {
                    this.$notify.error({
                        title: '错误',
                        message: error.message,
                        duration: 0
                    });
                    throw error;
                })
                .fin(function () {
                    $elem._req_block = false;
                })
                .done();
            },
            getContentLink(item) {
                let url = item.assign_url || item.url || 'javascript:;'
                let isAd = item.extend && item.extend.skip_ad && item.extend.skip_ad == 1
                return `${url}?${isAd ? 'skip_ad=1' : ''}`;
            },
            getList() {
                let {filter} = this
//                this.$updateFilterStore();
                this.loading = true;
                quRequest.send(quRequest.Apis.qukan.content.getAdminContentList, filter)
                    .then(res => {
                        let data = res.data;
                        this.listData = data.data || [];
                        this.totalPage = data.total_page || 1;
                    })
                    .catch(err => {
                        this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                    })
                    .fin(() => {
                        this.loading = false;
                    }).done();
            },
            getCategorys() {
                quRequest.send(quRequest.Apis.qukan.content.getDefaultChannelList)
                    .then(res => {
                        let data = res.data
                        this.articleTypesMap = {};
                        data.forEach((type) => {
                            this.articleTypesMap[type.id] = type.name;
                        });
                        this.articleTypes = data;
                    })
                    .catch(err => {
                        this.$message.error(`获取分类数据失败：${ err.message || '未知错误' }`);
                        throw err;
                    }).done();
            }
        }
    }

</script>

<style lang="scss" type="text/scss">
    .ibox{
        margin-bottom: 25px;
        margin-top: 0;
        border-radius: 5px;
        box-shadow: 0 2px 3px hsla(0,0%,4%,.1), 0 0 0 1px hsla(0,0%,4%,.1);
        display: block;
        overflow: hidden;
        .ibox-content{
            .control{
                &:last-child{
                    margin-top:0.75rem;
                    .el-pagination{
                        padding:0;
                    }
                }
            }
        }
    }
    .el-tag {
        cursor: pointer;
    }
    .ibox-filter {
        .el-tag {
            margin-right: 6px;
        }
    }
    .content-manage-box {
        .el-table__body-wrapper {
            z-index: 0;
        }

        .filter-item {
            display: inline-block;
            margin-right: 20px;
        }

        .cell-cover {
            position: relative;
            .covers {
                height: 40px;
                img {
                    position: absolute;
                    border-radius: 3px;
                    border: 1px #dfdfdf solid;
                    box-shadow: 0 0 3px rgba(0, 0, 0, .125);
                    &:nth-child(1) {
                        left: 0;
                        z-index: 100;
                    }
                    &:nth-child(2) {
                        left: 6px;
                        z-index: 99;
                    }
                    &:nth-child(3) {
                        left: 12px;
                        z-index: 98;
                    }
                    &:nth-child(4) {
                        left: 18px;
                        z-index: 97;
                    }
                }
            }
        }

        .props-table-expand {
            font-size: 0;
            label {
                width: 90px;
                color: #99a9bf;
            }
            .el-form-item {
                margin-right: 0;
                margin-bottom: 0;
                width: 30%;
            }
        }

        .el-table {
            .cell {
                padding: 18px 5px 10px;
                .el-button {
                    margin-left: 10px;
                    margin-top: 5px;
                }

                .el-button + .el-button {
                    margin-left: 10px;
                }
            }
        }

        .cell-badge {
            position: relative;
            .content-badge {
                position: absolute;
                top: -2px;
                right: 5px;
            }
            .el-tag {
                height: 18px;
                line-height: 16px;
            }
        }
    }
</style>



