import q from 'q';
import QuRequest from '../core/QuRequest';
import ArticleStatus from './ArticleStatus';

const quRequest = new QuRequest();

class Content {
    constructor() {
        this._getDenyAreasPromise = null;
        this.articleStatus = ArticleStatus;
    }

    get delayStatus() {
        return {
            "1": "延迟",
            "2": "非延迟",
        };
    }

    get sourceNameTypes() {
        return {
            '1': '编辑发布',
            '2': '合作媒体',
            '3': '自媒体'
        }
    }

    get tips() {
        return ["热门", "实时"];
    }

    getTypes() {
        if (this._getDenyAreasPromise) {
            return this._getDenyAreasPromise;
        }

        this._getDenyAreasPromise = quRequest.send(quRequest.Apis.qukan.content.getDefaultChannelList)
        .then(res => {
            let data = res.data;
            let maps = {};
            data.forEach((type) => {
                maps[type.id] = type.name;
            });
            return {
                maps,
                data
            }
        })
        .catch(err => {
            this._getDenyAreasPromise = null;
            throw err;
        });

        return this._getDenyAreasPromise;
    }

    getContentLink(item) {
        let url = item.assign_url || item.url || 'javascript:;'
        let isAd = item.extend && item.extend.skip_ad && item.extend.skip_ad == 1
        return `${url}?${isAd ? 'skip_ad=1' : ''}`;
    }
}

export default Content;



