require("./style.less");
let template = require("raw-loader!./temp.tpl");
const $ = require('jquery');

class PreviewCover {
    constructor(params) {
        let _this = this;
        let defaultOptions = {
            autoClose: true,
            container: 'body',
        };

        const options = Object.assign(defaultOptions, params);
        const $elem = $(template);

        options.$container = $(options.container);
        options.$container.append($elem);

        this.options = options;
        this.$elem = $elem;
        this.$previewCover = $elem.find('.article-preview-photo');
        this.$previewMask = $elem.find('.article-preview-photo-mask');

        if (options.autoClose) {
            $('body').on('keyup', (event) => {
                if (event.keyCode === 27) {
                    _this.close();
                }
            });
        }

        this.$previewMask.on('click', () => {
            this.close();
        })
    }

    getPos($target, margin) {
        let $container = this.options.$container;
        if (!$target) {
            return {
                top: 0 + margin.top,
                left: 0 + margin.left
            };
        }

        let t = $target.offset();
        let c = $container.offset();

        if (c && 'top' in c && 'left' in c) {
            t.left = t.left - c.left;
            t.top = t.top - c.top;
        }

        return {
            top: t.top + margin.top,
            left: t.left + $target.width() + margin.left
        };
    }

    show(target, urls, margin) {
        let _this = this;
        let $target = $(target);
        let offset = this.getPos($target, margin);

        // 填充 list
        let list = [];
        urls.forEach((url, idx) => {
            let li = `<li><a href="${url}" target="_blank"><img src="${url}" alt=""></a></li>`;
            list.push(li);
        });

        this.$previewCover.find('ul').html(list.join(''));
        this.$previewCover.css({
            top: offset.top,
            left: offset.left
        }).show();
        this.$previewMask.show();
    }

    close() {
        this.$previewCover.hide();
        this.$previewMask.hide();
    }

    destory() {
        this.$elem.remove();
    }

}

export default PreviewCover;



