class ArticleStatus {
    constructor() {
        this._unpublished = '1';
        this._published = '2';
        this._deleted = '3';

        this._statusMaps = {
            [this._unpublished]: '未发布',
            [this._published]: '已发布',
            [this._deleted]: '已删除'
        }
    }
    
    get unpublished() {
        return this._unpublished;
    }
    
    get published() {
        return this._published;
    }
    
    get deleted() {
        return this._deleted;
    }

    get maps() {
        return this._statusMaps;
    }
}

export default new ArticleStatus();



